"use client";
import { useState } from "react";
import Image from "next/image";
import { Button } from "../ui/button";
import clsx from "clsx";
import { useRouter } from "next/navigation";


type Student = {
  id: string;
  name: string;
  avatar?: string;
  phone: string;
  gender: "Male" | "Female";
  parentPhone: string;
};

const initialStudents: Student[] = [
  {
    id: "1",
    name: "Darrell Steward",
    avatar: "/avatars/darrell.jpg",
    phone: "+85515465165",
    gender: "Male",
    parentPhone: "90324423789",
  },
  {
    id: "2",
    name: "Marc Atenson",
    avatar: "/avatars/marc.jpg",
    phone: "+85521654987",
    gender: "Male",
    parentPhone: "90324423789",
  },
  {
    id: "3",
    name: "Susan Drake",
    phone: "+85545464551",
    gender: "Female",
    parentPhone: "90324423789",
  },
  {
    id: "4",
    name: "Lamine Yamal",
    avatar: "/avatars/lamine.jpg",
    phone: "+85533566457",
    gender: "Male",
    parentPhone: "90324423789",
  },
  {
    id: "5",
    name: "Aaron Judge",
    phone: "+85564689751",
    gender: "Male",
    parentPhone: "90324423789",
  },
  {
    id: "6",
    name: "Jasmine William",
    phone: "+85578234658",
    gender: "Female",
    parentPhone: "90324423789",
  },
];

function getInitials(name: string) {
  return name
    .split(" ")
    .map((n) => n[0])
    .join("")
    .toUpperCase();
}

const ITEMS_PER_PAGE = 8;

export default function StudentTable() {
  const [students, setStudents] = useState<Student[]>(initialStudents);
  const [currentPage, setCurrentPage] = useState(1);
  const [deleteModalOpen, setDeleteModalOpen] = useState(false);
  const [selectedStudent, setSelectedStudent] = useState<Student | null>(null);
  const router = useRouter();

  const totalPages = Math.ceil(students.length / ITEMS_PER_PAGE);
  const startIdx = (currentPage - 1) * ITEMS_PER_PAGE;
  const currentItems = students.slice(startIdx, startIdx + ITEMS_PER_PAGE);

  const handleEdit = (student: Student) => {
    router.push(`/dashboard/school/student/edit/${student.id}`);
  };

  const handleDelete = (student: Student) => {
    setSelectedStudent(student);
    setDeleteModalOpen(true);
  };

  const confirmDelete = () => {
    if (!selectedStudent) return;
    setStudents((prev) => prev.filter((s) => s.id !== selectedStudent.id));
    setDeleteModalOpen(false);
    setSelectedStudent(null);
  };

  return (
    <div className="space-y-4 relative">
      {/* Table */}
      <div className="overflow-x-auto rounded-xl border border-gray-200 shadow-sm">
        <table className="min-w-full text-sm text-left">
          <thead className="bg-gray-50 text-gray-600 uppercase text-xs hidden sm:table-header-group">
            <tr>
              <th className="px-6 py-4">ឈ្មោះ</th>
              <th className="px-6 py-4">លេខទូរស័ព្ទមាតាបិតា</th>
              <th className="px-6 py-4">ភេទ</th>
              <th className="px-6 py-4">លេខអត្តសញ្ញាណបុគ្គល</th>
              <th className="px-6 py-4">បញ្ជារ</th>
            </tr>
          </thead>
          <tbody className="divide-y divide-gray-100 bg-white">
            {currentItems.map((student) => (
              <tr key={student.id}>
                <td className="px-6 py-4 flex items-center gap-3">
                  {student.avatar ? (
                    <Image
                      src={student.avatar}
                      alt={student.name}
                      width={36}
                      height={36}
                      className="rounded-full object-cover"
                    />
                  ) : (
                    <div className={clsx(
                      "w-9 h-9 rounded-full flex items-center justify-center font-bold text-lg",
                      student.gender === "Male" ? "bg-blue-100 text-blue-600" : "bg-pink-100 text-pink-600"
                    )}>
                      {getInitials(student.name)}
                    </div>
                  )}
                  <div className="text-sm font-medium">{student.name}</div>
                </td>
                <td className="px-6 py-4">{student.phone}</td>
                <td className="px-6 py-4">
                  <span className={clsx(
                    "px-3 py-1 rounded-full text-xs font-semibold",
                    student.gender === "Male"
                      ? "bg-blue-100 text-blue-600"
                      : "bg-pink-100 text-pink-600"
                  )}>
                    {student.gender}
                  </span>
                </td>
                <td className="px-6 py-4">{student.parentPhone}</td>
                <td className="px-6 py-4 flex gap-2">
                  <button
                    onClick={() => handleEdit(student)}
                    className="text-blue-500 hover:text-blue-700"
                    title="Edit"
                  >
                   <Pencil size={18} />
                  </button>
                  <button
                    onClick={() => handleDelete(student)}
                    className="text-red-500 hover:text-red-700"
                    title="Delete"
                  >
                    <svg width={18} height={18} fill="none" stroke="currentColor" strokeWidth={2} viewBox="0 0 24 24">
                      <path d="M6 19a2 2 0 002 2h8a2 2 0 002-2V7H6v12zM19 7V5a2 2 0 00-2-2H7a2 2 0 00-2 2v2" />
                    </svg>
                  </button>
                </td>
              </tr>
            ))}
          </tbody>
        </table>
      </div>

      {/* Pagination */}
      <div className="flex items-center gap-4 justify-center mt-4">
        <Button
          className="bg-[#25388C] hover:bg-[#1e2e6d]"
          onClick={() => setCurrentPage((p: number) => Math.max(p - 1, 1))}
          disabled={currentPage === 1}
        >
          Previous
        </Button>
        <span className="text-sm text-gray-600">
          Page {currentPage} of {totalPages}
        </span>
        <Button
          className="bg-[#25388C] hover:bg-[#1e2e6d]"
          onClick={() => setCurrentPage((p: number) => Math.min(p + 1, totalPages))}
          disabled={currentPage === totalPages}
        >
          Next
        </Button>
      </div>

      {/* Delete Modal */}
      {deleteModalOpen && selectedStudent && (
        <div className="fixed inset-0 z-50 bg-black/50 flex items-center justify-center">
          <div className="bg-white p-6 rounded-lg shadow-md w-[90%] max-w-sm text-center">
            <p className="font-semibold text-gray-800 mb-6">
              តើអ្នកចង់លុបបុគ្គលនេះមែនទេ?
            </p>
            <div className="flex justify-center gap-4">
              <Button
                variant="secondary"
                onClick={() => setDeleteModalOpen(false)}
              >
                បោះបង់
              </Button>
              <Button
                variant="destructive"
                onClick={confirmDelete}
              >
                លុបបុគ្គល
              </Button>
            </div>
          </div>
        </div>
      )}
    </div>
  );
}